@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  position: relative;
}

.stopwatch__min {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  position: absolute;
  bottom: 50%;
  left: 48%;
  transform: translateX(-50%);
}

.stopwatch__min--speed-up {
  animation: 600s steps(60, end) infinite move;
}

.stopwatch__sec {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  bottom: 50%;
  left: 49%;
  transform: translateX(-50%);
}

.stopwatch__sec--speed-up {
  animation: 10s linear infinite move;
}

.stopwatch__cicle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
